.container {
    display: inline-block;

    .select {
        /**    **/
        height: 48px;
        box-sizing: border-box;

        /**   **/
        border: 1px solid #E4E7EC;
        border-radius: 6px;

        /**    **/
        padding-top: 14px;
        padding-left: 15px;

        /**    **/
        font-family: Spoqa Han Sans Neo;
        font-size: 16px;
        line-height: 20px;
        color: #98A2B3;

        /**   () **/
        background-repeat: no-repeat;
        background-position: 98% 50%;

        /**     **/
        cursor: pointer;

        /**    **/
        display: inline-block;
    }

    .options {
        /**    **/
        position: absolute;
        z-index: 100;

        /**    **/
        margin-top: 8px;

        /**    **/
        //padding: 5px 0;
        
        /**   **/
        width: 790px;

        /**   **/
        border: 1px solid #E4E7EC;
        box-sizing: border-box;
        border-radius: 6px;

        /**   **/
        background-color: #FFFFFF;

        .option {
            /**    **/
            padding: 10px 0 10px 15px;

            /**   **/
            font-family: Spoqa Han Sans Neo;
            font-size: 16px;
            line-height: 20px;
            color: #101828;

            /**   **/
            border-top: 1px solid #E4E7EC;

            /**     **/
            cursor: pointer;
        }

        .option:nth-child(1) {
            /**    **/
            padding-top: 15px;

            /**   **/
            border-top: none;
        }

        .option:nth-last-child(1) {
            /**    **/
            padding-bottom: 15px;
        }

        .group {
            .groupName {
                /**   **/
                background-color: #F2F4F7;

                /**   **/
                font-family: Spoqa Han Sans Neo;
                font-size: 14px;
                line-height: 18px;
                color: #667085;

                /**    **/
                padding: 14px 0 14px 14px;
            }

            .groupOption {
                /**   **/
                background-color: #FFFFFF;

                /**    **/
                padding: 10px 0 10px 15px;

                /**   **/
                font-family: Spoqa Han Sans Neo;
                font-size: 16px;
                line-height: 20px;
                color: #101828;

                /**   **/
                border-top: 1px solid #E4E7EC;

                /**     **/
                cursor: pointer;
            }

            .groupName + .groupOption {
                border-top: none;
            }
        }

        .group:nth-last-child(1) {
            /**    **/
            padding-bottom: 8px;
        }
    }

    .on {
        display: block;
    }

    .off {
        display: none;
    }
}