.container {
    /**    **/
    display: inline-block;

    /**    **/
    width: 243px;
    height: 24px;
    
    /**    **/
    margin: 7px;

    /**   **/
    font-family: Spoqa Han Sans Neo;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: -0.02em;
    color: #98A2B3;

    .checkBtn {
        /**    **/
        width: 18px;
        height: 18px;

        /**   **/
        background-color: #FFFFFF;

        /**   **/
        box-sizing: border-box;
        border-radius: 6px;

        /**    **/
        margin-right: 10px;
        
        /**   **/
        background-repeat: no-repeat;
        background-position: center;
    }

    .noSelected {
        /**   **/
        background-image: url(../../images/check-noselect.svg);

        /**   **/
        border: 1px solid #E4E7EC;
    }

    .selected {
        /**   **/
        border: 1px solid #1A5DD1;

        /**   **/
        background-color: #1A5DD1;

        /**   **/
        background-image: url(../../images/check-select.svg);
    }
}