@import '../../../common/font';
@import '../../../common/element';

.container {
    /**    **/
    width: 1366px;

    /**    **/
    margin: 0 auto;

    /**    **/
    padding: 130px 103px 90px 103px;

    /**   **/
    display: grid;
    grid-template-columns: 250px auto;
    column-gap: 30px;
    row-gap: 26px;

    .title {
        /**    **/
        grid-column-start: 1;
        grid-column-end: 3;
    }

    .sub {
        /**   **/
        background-color: #FFFFFF;

        /**   **/
        border: 1px solid #E4E7EC;
        border-radius: 6px;

        /**    **/
        // padding: 20px;

        .top {
            /**    **/
            padding: 20px;

            /**   **/
            border-bottom: 1px solid #E4E7EC;

            .circle {
                /**    **/
                width: 80px;
                height: 80px;
                box-sizing: border-box;

                /**   **/
                background-color: #F2F4F7;

                /**   **/
                border: 1px solid #E4E7EC;
                border-radius: 40px;

                /**    **/
                text-align: center;
                padding: 31px 0;
            }

            .editImg {
                /**     **/
                float: right;

                /**   **/
                background-image: url(../../../../images/edit_black_24dp.svg);
                background-repeat: no-repeat;
                background-position: center;

                /**    **/
                width: 20px;
                height: 20px;
            }

            .box {
                /**    **/
                margin-top: 20px;

                /**   **/
                display: grid;
                grid-template-rows: auto auto;
                row-gap: 8px;
            }
        }

        .middle {
            /**    **/
            padding: 34px 20px;

            /**   **/
            border-bottom: 1px solid #E4E7EC;

            /**   **/
            display: grid;
            grid-template-rows: auto auto auto;
            row-gap: 28px;
        }

        .bottom {
            /**    **/
            padding: 15px 20px;
        }
    }

    .content {
        /**   **/
        display: grid;
        row-gap: 30px;

        .common {
            /**   **/
            border: 1px solid #E4E7EC;
            border-radius: 6px;
            
            /**   **/
            background-color: #FFFFFF;
        }

        .right {
            float: right;
        }

        .public {
            /**    **/
            padding: 20px 30px;

            .toggleBox {
                /**   **/
                display: grid;
                grid-template-columns: auto auto;
                column-gap: 40px;

                > span {
                    /**    **/
                    margin: auto 0;
                }
            }
        }

        .introduction {
            /**    **/
            padding: 30px;

            /**   **/
            display: grid;
            row-gap: 20px;

            /**   **/
            border-bottom: 1px solid #E4E7EC;
        }

        .likeSection {
            /**    **/
            padding: 30px;

            /**   **/
            display: grid;
            grid-template-columns: auto auto;

            > .subSection {
                /**   **/
                display: grid;
                row-gap: 20px;
            } 
        }

        .positionSection {
            /**   **/
            display: grid;
            row-gap: 20px;

            .positionList {
                /**   **/
                display: grid;
                row-gap: 25px;

                .position {
                    /**   **/
                    display: grid;
                    row-gap: 14px;
                }
            }
        }

        .portfolioSection {
            /**   **/
            display: grid;
            row-gap: 20px;

            .portfolio {
                /**    **/
                width: 40px;
                height: 40px;

                /**    **/
                margin-right: 12px;
            }
        }

        .item {
            padding: 30px;
        }
    }
}