@import url(//spoqa.github.io/spoqa-han-sans/css/SpoqaHanSansNeo.css);

@mixin Font($color: black, $lineHeight: 20px) {
    font-family: Spoqa Han Sans Neo;
    font-size: 16px;
    line-height: $lineHeight;
    color: $color;
}

@mixin SubSection {
    margin-top: 40px;
}

.container {
    margin: 70px auto;
    margin-top: 130px;
    width: 1160px;

    .card {
        /**    **/
        position: fixed;
        top: 130px;
        left: calc(50% + 240px);

        /**    **/
        padding: 20px;

        /**    **/
        width: 340px;
        box-sizing: border-box;

        /**   **/
        border-radius: 6px;

        /**   **/
        box-shadow: 0px 8px 30px rgba(16, 24, 40, 0.02), 
        0px 8px 24px rgba(16, 24, 40, 0.02), 
        0px 4px 12px rgba(16, 24, 40, 0.02), 
        0px 2px 6px rgba(16, 24, 40, 0.02);

        .hit {
            /**    **/
            margin-top: 8px;

            /**   **/
            font-family: Spoqa Han Sans Neo;
            font-size: 18px;
            font-weight: bold;
            line-height: 23px;
            color: #101828;
        }

        .share {
            /**   **/
            background-image: url(../../images/share_black_24dp.svg);
            background-repeat: no-repeat;
            background-position: center;

            /**   **/
            border: 1px solid #E4E7EC;
            border-radius: 20px;
            box-sizing: border-box;

            /**    **/
            width: 40px;
            height: 40px;

            /**     **/
            float: right;
        }

        .member {
            /**    **/
            margin-top: 20px;

            .title {
                /**    **/
                margin-bottom: 18px;

                /**    **/
                display: block;

                /**   **/
                font-family: Spoqa Han Sans Neo;
                font-size: 14px;
                line-height: 18px;
                color: #101828;
            }

            .memberImg {
                /**    **/
                width: 50px;
                height: 50px;
                box-sizing: border-box;

                /**   **/
                border: 1px solid #E4E7EC;
                border-radius: 25px;

                /**   **/
                font-family: Spoqa Han Sans Neo;
                font-size: 14px;
                line-height: 18px;
                text-align: center;
                color: #101828;

                /**    **/
                margin-right: 12px;

                /**    **/
                padding: 15px 0;
            }

            .memberNum {
                /**    **/
                width: 50px;
                height: 50px;
                box-sizing: border-box;

                /**   **/
                border: 1px solid #E4E7EC;
                border-radius: 25px;

                /**   **/
                font-family: Spoqa Han Sans Neo;
                font-size: 16px;
                line-height: 20px;
                text-align: center;
                color: #101828;

                /**    **/
                padding: 15px 0;
            }
        }

        .btnBox {
            /**    **/
            margin-top: 20px;

            .btn {
                /**    **/
                width: 300px;
                box-sizing: border-box;
    
                /**   **/
                border-radius: 6px;
    
                /**   **/
                font-family: Spoqa Han Sans Neo;
                font-size: 16px;
                line-height: 20px;

                /**    **/
                margin-top: 10px;
    
                /**    **/
                padding: 14px 0;
            }

            .primary {
                /**   **/
                border: none;
    
                /**   **/
                background-color: #175CD3;
    
                /**    **/
                color: #FFFFFF;
            }
    
            .secondary {
                /**   **/
                border: 1px solid #E4E7EC;
    
                /**   **/
                background-color: #FFFFFF;
    
                /**    **/
                color: #175CD3;
            }
    
            .bookMark {
                /**   **/
                background-image: url(../../images/book_mark_turn_in_not.svg);
                background-repeat: no-repeat;
                background-position: 33% 50%;
            }
        }
    }

    .badgeSection {
        .badge {
            /**   **/
            border: none;
            border-radius: 6px;

            /**   **/
            background-color: #F2F4F7;

            /**   **/
            font-family: Spoqa Han Sans Neo;
            font-size: 12px;
            line-height: 20px;
            color: #1D2939;

            /**    **/
            margin-right: 12px;

            /**    **/
            padding: 4px 8px;
        }

        .recruit {
            /**   **/
            background-color: #E2EEFE;

            /**   **/
            color: #4090F7;
        }
    }

    .titleSection {
        /**    **/
        margin-top: 20px;

        /**    **/
        padding-bottom: 40px;

        /**    **/
        width: 790px;

        .title {
            /**   **/
            font-family: Spoqa Han Sans Neo;
            font-size: 32px;
            line-height: 40px;
            font-weight: bold;
            color: #101828;          
        }
    }

    .profileSection {
        /**    **/
        margin-top: 60px;

        .title {
            /**   **/
            font-family: Spoqa Han Sans Neo;
            font-size: 18px;
            line-height: 23px;
            font-weight: bold;
            color: #101828;   
        }

        .content {
            /**    **/
            margin-top: 20px;

            /**    **/
            padding: 20px;

            /**    **/
            width: 790px;
            overflow: auto;

            /**    **/
            display: grid;
            grid-template-columns: 48px 530px auto;
            column-gap: 13px;

            /**   **/
            border-radius: 6px;
            box-shadow: 0px 8px 24px rgba(16, 24, 40, 0.02), 
            0px 4px 12px rgba(16, 24, 40, 0.02), 
            0px 2px 6px rgba(16, 24, 40, 0.02);

            .profileImg {
                /**   **/
                grid-row-start: 1;
                grid-row-end: 3;

                /**    **/
                width: 48px;
                height: 48px;

                /**   **/
                border: 1px solid #E4E7EC;
                border-radius: 24px;

                /**   **/
                font-family: Spoqa Han Sans Neo;
                font-size: 14px;
                line-height: 18px;
                color: #101828;
                padding: 15px 0;
                text-align: center;
            }

            .description {
                /**    **/
                display: inline-block;

                /**    **/
                margin-left: 2px;

                /**    **/
                width: 530px;

                /**   **/
                font-family: Spoqa Han Sans Neo;
                font-size: 16px;
                line-height: 26px;
                color: #101828;
            }
            
            .moreSection {
                /**    **/
                text-align: right;

                .more {
                    /**   **/
                    font-family: Spoqa Han Sans Neo;
                    font-size: 16px;
                    line-height: 20px;
                    color: #175CD3;
                }                
            }

            .bottom {
                /**   **/
                grid-column-start: 2;
                grid-column-end: 4;
    
                .tag {
                    /**   **/
                    font-family: Spoqa Han Sans Neo;
                    font-size: 16px;
                    line-height: 20px;
                    color: #98A2B3;
                    letter-spacing: -0.02em;
    
                    /**    **/
                    margin-right: 12px;
                }
            }
        }
    }

    .subSection {
        margin-top: 40px;
    }

    .introduction {
        /**    **/
        margin-top: 40px;

        /**    **/
        width: 790px;

        .title {
            /**    **/
            display: block;

            /**   **/
            font-family: Spoqa Han Sans Neo;
            font-size: 18px;
            font-weight: bold;
            line-height: 23px;
            color: #101828;
        }

        .content {
            /**    **/
            margin-top: 20px;

            /**   **/
            font-family: Spoqa Han Sans Neo;
            font-size: 16px;
            line-height: 26px;
            color: #101828;
        }
    }

    .process {
        /**    **/
        margin-top: 40px;

        .title {
            /**   **/
            font-family: Spoqa Han Sans Neo;
            font-size: 18px;
            line-height: 23px;
            font-weight: bold;
            color: #101828;
        }

        .content {
            /**    **/
            margin-top: 20px;

            /**    **/
            font-family: Spoqa Han Sans Neo;
            font-size: 16px;
            line-height: 20px;
            color: #101828;
        }
    }

    .location {
        /**    **/
        margin-top: 40px;

        .title {
            /**   **/
            font-family: Spoqa Han Sans Neo;
            font-size: 18px;
            line-height: 23px;
            font-weight: bold;
            color: #101828;
        }

        .content {
            /**    **/
            margin-top: 20px;

            /**    **/
            font-family: Spoqa Han Sans Neo;
            font-size: 16px;
            line-height: 20px;
            color: #101828;
        }
    }

    .expectPeriod {
        /**    **/
        margin-top: 40px;

        .title {
            /**   **/
            font-family: Spoqa Han Sans Neo;
            font-size: 18px;
            line-height: 23px;
            font-weight: bold;
            color: #101828;
        }

        .content {
            /**    **/
            margin-top: 20px;

            /**    **/
            font-family: Spoqa Han Sans Neo;
            font-size: 16px;
            line-height: 20px;
            color: #101828;
        }
    }

    .role {
        /**    **/
        margin-top: 40px;

        /**    **/
        width: 790px;

        .title {
            /**   **/
            font-family: Spoqa Han Sans Neo;
            font-size: 18px;
            line-height: 23px;
            font-weight: bold;
            color: #101828;
        }

        .content {
            /**    **/
            margin-top: 5px;

            .positionContent {
                /**    **/
                margin-top: 15px;

                /**    **/
                padding: 20px;

                /**   **/
                box-shadow: 0px 8px 24px rgba(16, 24, 40, 0.02), 
                0px 4px 12px rgba(16, 24, 40, 0.02), 
                0px 2px 6px rgba(16, 24, 40, 0.02);
                border-radius: 6px;

                .top {
                    /**    **/
                    overflow: auto;

                    /**   **/
                    border-bottom: 1px dashed #E4E7EC;

                    .name {
                        /**    **/
                        margin: 10px 0 25px 0;

                        /**   **/
                        font-family: Spoqa Han Sans Neo;
                        font-size: 16px;
                        line-height: 20px;
                        color: #101828;
                    }

                    .number {
                        /**     **/
                        float: right;

                        /**    **/
                        margin: 10px 22px 0 0;

                        /**   **/
                        font-family: Spoqa Han Sans Neo;
                        font-size: 16px;
                        line-height: 20px;
                        color: #101828;
                    }

                    .btn {
                        /**     **/
                        float: right;

                        /**    **/
                        margin-right: 20px;

                        /**    **/
                        padding: 10px 15px;

                        /**   **/
                        border: 1px solid #E4E7EC;
                        box-sizing: border-box;
                        border-radius: 6px;

                        /**   **/
                        font-family: Spoqa Han Sans Neo;
                        font-size: 16px;
                        line-height: 20px;
                        color: #175CD3;
                    }

                    .complete {
                        /**   **/
                        background-color: #F2F4F7;

                        /**   **/
                        color: #98A2B3;
                    }
                }

                .bottom {
                    /**    **/
                    margin-top: 10px;

                    .tag {
                        /**   **/
                        font-family: Spoqa Han Sans Neo;
                        font-size: 16px;
                        line-height: 20px;
                        color: #98A2B3;
                        letter-spacing: -0.02em;

                        /**    **/
                        margin-left: 12px;
                    }
                }
            }
        }
    }

    .period {
        /**    **/
        margin-top: 40px;

        .title {
            /**    **/
            display: block;

            /**   **/
            font-family: Spoqa Han Sans Neo;
            font-size: 18px;
            line-height: 23px;
            font-weight: bold;
            color: #101828;
        }

        .content {
            /**    **/
            margin-top: 20px;

            /**    **/
            font-family: Spoqa Han Sans Neo;
            font-size: 16px;
            line-height: 20px;
            color: #101828;
        }
    }

    .tool {
        /**    **/
        margin-top: 40px;

        .title {
            /**    **/
            display: block;
            
            /**   **/
            font-family: Spoqa Han Sans Neo;
            font-size: 18px;
            line-height: 23px;
            font-weight: bold;
            color: #101828;
        }

        .content {
            /**    **/
            margin-top: 20px;

            /**    **/
            font-family: Spoqa Han Sans Neo;
            font-size: 16px;
            line-height: 20px;
            color: #101828;

            ul {
                /**    **/
                margin-left: 20px;

                > li {
                    /**    **/
                    list-style-type: disc;
                }
            }
        }
    }
}