.cell {
    /**    **/
    margin: 0;

    /**    **/
    padding-top: 12px;

    /**    **/
    display: inline-block;

    /**    **/
    width: 48px;
    height: 48px;
    
    /**   **/
    text-align: center;

    /**   **/
    font-family: Spoqa Han Sans Neo;
    font-size: 16px;
    line-height: 20px;
    color: #101828;

    /**     **/
    cursor: pointer;
}

.boundary {
    /**   **/
    background-color: #175CD3;

    /**   **/
    color: #FFFFFF;
}

.interval {
    /**   **/
    background-color: #EBF0FA;

    /**   **/
    color: #101828;
}