.container {
    /**    **/
    width: 1366px;

    /**    **/
    margin: 0 auto;
    margin-top: 130px;

    /**    **/
    padding: 0 103px;

    /**   **/
    display: grid;
    row-gap: 10px;

    h2 {
        font-size: 32px;
        line-height: 40px;
        color: #101828;
    }

    .buttonSection {
        display: grid;
        grid-template-columns: auto auto auto auto;
        grid-gap: 10px;
    }
}