@import '../../../common/font';
@import '../../../common/element';

.container {
    /**    **/
    width: 1366px;

    /**    **/
    margin: 0 auto;

    /**    **/
    padding: 130px 103px 90px 103px;

    /**   **/
    display: grid;
    grid-template-columns: 250px auto;
    column-gap: 30px;
    row-gap: 26px;

    .title {
        /**    **/
        grid-column-start: 1;
        grid-column-end: 3;
    }

    .sub {
        /**   **/
        background-color: #FFFFFF;

        /**   **/
        border: 1px solid #E4E7EC;
        border-radius: 6px;

        /**    **/
        // padding: 20px;

        .top {
            /**    **/
            padding: 20px;

            /**   **/
            border-bottom: 1px solid #E4E7EC;

            .circle {
                /**    **/
                width: 80px;
                height: 80px;
                box-sizing: border-box;

                /**   **/
                background-color: #F2F4F7;

                /**   **/
                border: 1px solid #E4E7EC;
                border-radius: 40px;

                /**    **/
                text-align: center;
                padding: 31px 0;
            }

            .editImg {
                /**     **/
                float: right;

                /**   **/
                background-image: url(../../../../images/edit_black_24dp.svg);
                background-repeat: no-repeat;
                background-position: center;

                /**    **/
                width: 20px;
                height: 20px;
            }

            .box {
                /**    **/
                margin-top: 20px;

                /**   **/
                display: grid;
                grid-template-rows: auto auto;
                row-gap: 8px;
            }
        }

        .middle {
            /**    **/
            padding: 34px 20px;

            /**   **/
            border-bottom: 1px solid #E4E7EC;

            /**   **/
            display: grid;
            grid-template-rows: auto auto auto;
            row-gap: 28px;
        }

        .bottom {
            /**    **/
            padding: 15px 20px;
        }
    }

    .content {
        /**   **/
        display: grid;
        row-gap: 30px;

        .section {
            /**    **/
            padding: 30px 20px;

            /**   **/
            border: 1px solid #E4E7EC;
            border-radius: 6px;

            /**   **/
            background-color: #FFFFFF;

            /**   **/
            display: grid;
            row-gap: 40px;

            .subSection {
                /**   **/
                display: grid;
                row-gap: 8px;

                .circle_120 {
                    @include circle($diameter: 120px, $radius: 60px);

                    /**    **/
                    text-align: center;
                    padding: 46px 0;

                    .filebox {
                        /**    **/
                        margin-top: 13px;

                        /**    **/
                        text-align: right;
                        
                        label[for="file"] {
                            /**    **/
                            display: inline-block;

                            /**    **/
                            width: 34px;
                            height: 34px;

                            /**   **/
                            background-color: #FFFFFF;

                            /**   **/
                            background-image: url(../../../../images/photo_camera_black_24dp.svg);
                            background-repeat: no-repeat;
                            background-position: center;

                            /**   **/
                            border: 1px solid #E4E7EC;
                            border-radius: 17px;

                            /**     **/
                            cursor: pointer;
                        }

                        input[type="file"] {
                            display: none;
                        }
                    }
                }

                .nickName {
                    /**   **/
                    border: 1px solid #E4E7EC;
                    border-radius: 6px;

                    /**    **/
                    padding: 14px 0 14px 15px;
                }
            }
        }

        .btnBox {
            display: grid;
            grid-template-columns: auto auto;
            column-gap: 10px;
            justify-content: flex-end;
        }
    }
}