.container {
    display: inline-block;
    
    .select {
        /**    **/
        margin-top: 8px;

        /**    **/
        padding: 14px 0;
        padding-left: 15px;

        /**    **/
        width: 790px;
        height: 48px;
        box-sizing: border-box;

        /**   **/
        border: 1px solid #E4E7EC;
        border-radius: 6px;

        /**   **/
        font-family: Spoqa Han Sans Neo;
        font-size: 16px;
        line-height: 20px;
        color: #98A2B3;

        /**     **/
        cursor: pointer;
    }

    .calendarBox {
        /**     **/
        display: flex;

        /**   **/
        background-color: #FFFFFF;

        /**    **/
        margin-top: 8px;

        /**   **/
        border: 1px solid #E4E7EC;
        border-radius: 6px;

        /**    **/
        width: 790px;
        box-sizing: border-box;

        .calendar {
            /**    **/
            width: 336px;
            box-sizing: border-box;

            .title {
                /**    **/
                margin-top: 20px;
                margin-bottom: 10px;

                /**   **/
                font-family: Spoqa Han Sans Neo;
                font-weight: bold;
                font-size: 18px;
                line-height: 20px;
                color: #101828;

                /**    **/
                text-align: center;
            }

            .dayOfWeeks {
                .dayOfWeek {
                    /**    **/
                    display: inline-block;

                    /**    **/
                    width: 48px;

                    /**   **/
                    font-family: Spoqa Han Sans Neo;
                    font-size: 14px;
                    line-height: 18px;
                    color: #98A2B3;

                    /**   **/
                    text-align: center;
                }
            }

            .content {
                /**    **/
                margin-top: 10px;

                .week {
                    .day {
                        /**    **/
                        margin: 0;

                        /**    **/
                        padding-top: 12px;

                        /**    **/
                        display: inline-block;

                        /**    **/
                        width: 48px;
                        height: 48px;
                        
                        /**   **/
                        text-align: center;

                        /**   **/
                        font-family: Spoqa Han Sans Neo;
                        font-size: 16px;
                        line-height: 20px;

                        color: #101828;
                    }

                    .boundary {
                        /**   **/
                        background-color: #175CD3;

                        /**   **/
                        color: #FFFFFF;
                    }

                    .interval {
                        /**   **/
                        background-color: #EBF0FA;

                        /**   **/
                        color: #101828;
                    }
                }
            }
        }

        .left {
            /**    **/
            margin-right: 29.5px;
        }

        .right {
            /**    **/
            margin-left: 29.5px;
        }

        .leftArrow {
            /**    **/
            width: 28px;

            & > button {
                /**    **/
                margin-top: 20px;
                margin-left: 20px;

                /**    **/
                width: 20px;
                height: 20px;

                /**   **/
                border: none;

                /**   **/
                background-color: #FFFFFF;

                /**     **/
                cursor: pointer;
            }
        }

        .rightArrow {
            /**    **/
            width: 29px;

            & > button {
                /**    **/
                float: right;
                
                /**    **/
                margin-top: 20px;
                margin-right: 20px;

                /**    **/
                width: 20px;
                height: 20px;

                /**   **/
                border: none;

                /**   **/
                background-color: #FFFFFF;

                /**     **/
                cursor: pointer;
            }
        }
    }
}