.container {
    .title {
        /**    **/
        width: 790px;
        height: 48px;

        /**   **/
        font-family: Spoqa Han Sans Neo;
        font-size: 16px;
        line-height: 20px;
        color: #98A2B3;

        /**   **/
        border: 1px solid #E4E7EC;
        box-sizing: border-box;
        border-radius: 6px;

        /**   **/
        background-color: #FFFFFF;

        /**    **/
        padding: 14px 0 14px 15px;

        /**   **/
        background-image: url(../../images/expand_more_black_24dp.svg);
        background-repeat: no-repeat;
        background-position: 98% 50%;

        /**    **/
        cursor: pointer;
    }

    .content {
        /**    **/
        display: inline-block;

        /**   **/
        width: 790px;

        /**    **/
        margin-top: 8px;

        /**   **/
        border: 1px solid #E4E7EC;
        border-radius: 6px;

        /**   **/
        background-color: #FFFFFF;

        .list {
            /**    **/
            padding: 8px;
            ul {
                li {
                    /**    **/
                    display: inline-block;
    
                    /**    **/
                    width: 243px;
                    height: 24px;
                    
                    /**    **/
                    margin: 7px;
    
                    /**   **/
                    font-family: Spoqa Han Sans Neo;
                    font-size: 16px;
                    line-height: 20px;
                    letter-spacing: -0.02em;
                    color: #98A2B3;
    
                    .checkBtn {
                        /**    **/
                        width: 18px;
                        height: 18px;
    
                        /**   **/
                        background-color: #FFFFFF;
    
                        /**   **/
                        box-sizing: border-box;
                        border-radius: 6px;
    
                        /**    **/
                        margin-right: 10px;
                        
                        /**   **/
                        background-repeat: no-repeat;
                        background-position: center;
                    }

                    .noSelected {
                        /**   **/
                        background-image: url(../../images/check-noselect.svg);

                        /**   **/
                        border: 1px solid #E4E7EC;
                    }

                    .selected {
                        /**   **/
                        border-color: #333;

                        /**   **/
                        background-image: url(../../images/check-select.svg);
                    }
                }
            }
        }
    }
}