.calendar {
    /**    **/
    width: 336px;
    box-sizing: border-box;

    .title {
        /**    **/
        margin-top: 20px;
        margin-bottom: 10px;

        /**   **/
        font-family: Spoqa Han Sans Neo;
        font-weight: bold;
        font-size: 18px;
        line-height: 20px;
        color: #101828;

        /**    **/
        text-align: center;
    }

    .dayOfWeeks {
        .dayOfWeek {
            /**    **/
            display: inline-block;

            /**    **/
            width: 48px;

            /**   **/
            font-family: Spoqa Han Sans Neo;
            font-size: 14px;
            line-height: 18px;
            color: #98A2B3;

            /**   **/
            text-align: center;
        }
    }

    .content {
        /**    **/
        margin-top: 10px;

        .week {
            .day {
                /**    **/
                margin: 0;

                /**    **/
                padding-top: 12px;

                /**    **/
                display: inline-block;

                /**    **/
                width: 48px;
                height: 48px;
                
                /**   **/
                text-align: center;

                /**   **/
                font-family: Spoqa Han Sans Neo;
                font-size: 16px;
                line-height: 20px;
                color: #101828;

                /**     **/
                cursor: pointer;
            }

            .boundary {
                /**   **/
                background-color: #175CD3;

                /**   **/
                color: #FFFFFF;
            }

            .interval {
                /**   **/
                background-color: #EBF0FA;

                /**   **/
                color: #101828;
            }
        }
    }
}

.left {
    /**    **/
    margin-right: 29.5px;
}

.right {
    /**    **/
    margin-left: 29.5px;
}